<template>

  <router-view
  id="chooseGame"
  :carts="chooseGamePageData"
  :couples-data="couplesData"
  ></router-view>
  <!--<mainMenu
  
  
  ></mainMenu>--->
</template>
<script>
import MainMenu from './components/MainMenu.vue';
export default {
  components: { MainMenu },
  created() {
    this.getChooseGamePageData()
    this.getCouplesData()
  },
  data() {
    return {
      chooseGamePageData:[],
      couplesData: [],
    }
  },
  methods: {
    async getChooseGamePageData() {
      let res = await fetch('gamecards.json')
      let data = await res.json()
      this.chooseGamePageData = data;
    },
    async getCouplesData() {
      let res = await fetch('couples.json')
      let data = await res.json()
      this.couplesData = data;
    },
    
  },

  
}

</script>

