<template>
 <div id="game-container">
    
    <div id="game">
        <div id="game-container-top">
            <div 
                id="show-character-part" 
                :style="['background-position: center' ,'background-repeat: no-repeat']" 
                >
                <img id="body-part" v-if="correctAnswer" :alt="correctAnswer.name" :src="
                 aImage"/>
            </div>
        </div>
        <div id="stats"><span> {{ `time:${time}s` }}</span><span> {{`score:${score}` }} </span></div>
         
        <div id="game-container-bottom">
            <div id="answers-container">
                <div :v-if="randomObjects > 3" v-for="(answer, index) in randomObjects"
                 class="answer"
                 :class="{'choosed': answer === PlayerAnswer}"
                 @click.prevent="!isGameLose? playerMove(answer) : 'lol'"
                 >
                    <span  class="answer-name"> 
                        {{ gameRunning == true && isGameLose == false? answer.name : 'Placek' }}
                    </span>
                </div>
            <!--  <button
                 id="guzikxD"
                 @click.prevent="gameRunning = true; startGame();
                /* console.log(correctAnswer); console.log(PlayerAnswer)*/"
                 :disabled="gameRunning == true"
                 >Start</button>
                 -->
            </div>
            <button
                id="guzikxDD"
                @click.prevent="
                isGameLose == true? time =+ 30 : time+0;
                gameRunning == true? submitAnswer() : startGame();
                this.gameRunning = true 
                console.log(randomObjects)"
                >
                {{ isGameLose == true? 'im noob' : gameRunning == false? "Start Game" : cm? "next" : "Submit"}}
            </button>
        </div>
        <div id="ramen"
        @click.prevent="ramenGifts()"
        >
            <img title="Click to remove one false answer" src="https://ih1.redbubble.net/image.1134765372.4459/st,small,507x507-pad,600x600,f8f8f8.jpg" alt="ramen">
            <div> {{ ramenCount }} </div>
        </div>
    </div>
 </div>
</template>
<script>
export default {
    created() {
        this.getAnimeCharacters()
        this.getAnimeCharactersNames()
    //    this.lol(this.animeCharacterNames)
    },
    data() {
        return { 
            gameRunning:false,
            animeCharacters:[],
            animeCharacterNames:[],
            randomObjects:[],
            correctAnswer:[],
            PlayerAnswer:[],
            aImage:[],
            answersWitchoutCorrect:[],
            cm: false,
            ramenCount: 3,
            score: 0,
            time: 20,
            isGameLose: false,
            intervalId:''
        }
        
    },
    methods: {
        startGame() {
            this.lol(this.animeCharacterNames)
            this.selectCorrectAnswer(this.randomObjects)
            this.setTimeOut()
            this.aImage = this.correctAnswer.partImage
            this.isGameLose = false
        },
        async getAnimeCharacters() {
            let res = await fetch('animeBodyPart.json')
            let data = await res.json()
            this.animeCharacters = data;
        }, async getAnimeCharactersNames() {
            let res = await fetch('animeNames.json')
            let data = await res.json()
            this.animeCharacterNames = data;
        },
        lol(array) {
            let randomObjects = []
            const arrayCopy = array;
            const randomIndex = Math.floor(Math.random() * this.animeCharacters.length);
            for (let i = 0; i < 3; i++) {
            const randomIndex = Math.floor(Math.random() * arrayCopy.length);
            const randomObject = arrayCopy[randomIndex]
            randomObjects.push(randomObject);
            }
            this.answersWitchoutCorrect = randomObjects
            randomObjects.push(this.animeCharacters[randomIndex])
            this.randomObjects = this.randomizeAnswers(randomObjects);
           // console.log(this.randomObjects)
           // console.log(this.correctAnswer)
        },
        selectCorrectAnswer(obj) {
            let answer = [];
            obj.forEach(obj => {
                if("partImage" in obj) {
                    answer = obj
                }
            })
            this.correctAnswer = answer;
           // console.log(this.correctAnswer)
        },
        playerMove(answer) {
            this.PlayerAnswer = answer
        },
        submitAnswer() {
            if (this.PlayerAnswer) {
                if (this.PlayerAnswer == this.correctAnswer) {
                    if(this.cm) {
                        this.lol(this.animeCharacterNames)
                        this.startGame()
                        this.cm = false
                    }else {
                        this.aImage = this.correctAnswer.mainImage
                        this.cm = true
                        this.score++
                        this.time = this.time +3
                    }
                }
            }
        },
        randomizeAnswers(arr) {
            const randomSort = () => Math.random() - 0.5;
            return arr.sort(randomSort)
        },
        ramenGifts() {
            const randomIndex = Math.floor(Math.random() * this.randomObjects.length);
            if (this.randomObjects[randomIndex] == this.correctAnswer) {
               if (this.randomObjects.length !== 1) {
                this.ramenGifts() 
               }
                return
            } if (this.ramenCount > 0 )
                this.randomObjects.splice(randomIndex, 1)
                console.log(this.randomObjects.length)
                this.ramenCount--
                console.log(this.ramenCount)
        },loseGame() {
            this.aImage = "https://cdn.discordapp.com/attachments/1199059115004153976/1200602105212317746/pobrane.jpg?ex=65c6c6d7&is=65b451d7&hm=2c59cde287fc5acc378220ec291805f4c03e7d376da17fbcf97b3d1db97d310d&"
            this.isGameLose = true
        },
        
        setTimeOut(){
            if (this.intervalId) {
                clearInterval(this.intervalId)
            }
                this.intervalId = setInterval(() => {
                if (this.time > 0) {
                    this.time--
                }
                if (this.time < 1) {
                    clearInterval(this.intervalId)
                    this.loseGame()
                    this.gameRunning = false
                }
            },1000)
            
        }, 
    }
}
</script>
<style>

#stats {
    position: absolute;
    right: 10px;
    width: 20vw;
    height: 100vh;
    top: 0;
    bottom: 0;
    right: 0;
    display: flex;
    flex-direction: column;

}
#stats span {
    font-size: 50px;
    color: white;
    font-weight: 700;
}
#guzikxD {
    width: 95px;
    height: 40px;
    border-bottom-right-radius: 18px;
    border-bottom-left-radius: 18px;
    background-color: rgb(189, 189, 189);
    border: rgba(53, 49, 49, 0.952) 4px solid;
    font-weight: 1000;
    font-size: 20px;
    position: absolute;
    top: 0;
    right: 100px;
}
#guzikxDD {
    padding: 4px 4px;
    background-color: rgb(189, 189, 189);
    border-radius: 12px;
    border: rgba(53, 49, 49, 0.952) 4px solid;
    font-weight: 1000;
    font-size: 20px;
    position: absolute;
    bottom: 23px;
   
    
}
#guzikxD:active {
    opacity: 0.8;
    transition: 0.2s;
}
#game {
   
}
#game-container {
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
}
#game-container-top {
    width: 100vw;
    height: 50vh;
    background-color: rgba(0, 255, 255, 0);
    display: flex;
    justify-content: center;
    align-items: end;
    position: relative;
}
#game-container-bottom {
    width: 100vw;
    height: 50vh;
    background-color: rgba(44, 59, 59, 0);
    display: flex;
    justify-content: center;
}

#show-character-part {
    width: 55vw;
    height: 95%;
    background-color: rgb(117, 116, 116);
    border-radius: 35px;
  /*  border-bottom-right-radius: 25px;
    border-bottom-left-radius: 25px;*/
    border: 8px rgb(109, 104, 104) solid;
    -webkit-box-shadow: inset 0px 0px 7px 6px rgba(66, 68, 90, 0.9);
    -moz-box-shadow: inset 0px 0px 7px 6px rgba(66, 68, 90, 0.9);
    box-shadow: inset 0px 0px 7px 6px rgba(66, 68, 90, 0.9);
}
#answers-container {
    width: 50vw;
    height: 95%;
    background-color: gray;
    border-radius: 15px;
    border-top-left-radius: 75px;
    border-top-right-radius: 75px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    border: 6px rgb(65, 65, 65) solid;
    -webkit-box-shadow: inset -1px 11px 43px -9px rgba(66, 68, 90, 0.9);
    -moz-box-shadow: inset -1px 11px 43px -9px rgba(66, 68, 90, 0.9);
    box-shadow: inset -1px 11px 43px -9px rgba(66, 68, 90, 0.9);
    position: relative;
}
.answer {
    width: 75%;
    height: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: beige;
    border-radius: 30px;
    margin: 15px 0 0 10px;
    align-self: center;
    justify-self: center;
    border: 6px rgb(94, 87, 87) solid;
    -webkit-box-shadow: 8px 11px 12px 12px rgba(66, 68, 90, 0.9);
    -moz-box-shadow: 8px 11px 12px 12px rgba(66, 68, 90, 0.9);
    box-shadow: 8px 11px 12px 12px rgba(66, 68, 90, 0.9);
}

.answer-name {
    font-weight: 700;
    font-size: 27px;
}

#body-part {
    width: 100%;
    height: 100%;
    border-radius: 35px;
}

.choosed {
    scale: 1.2;
}

#ramen {
    position: absolute;
    bottom: 100px;
    left: 30px;
}
#ramen img {
    height: 120px;
    width: 120px;
    border-radius: 100%;
}
#ramen div {
    color: aliceblue;
    font-size: 30px;
    font-weight: 700;
}

</style>