<template>
    <div id="container">
        <div class="container-fluid">
            <div
              v-for="(cart, index) in carts"
              class="game-cart"
              :class="{'choosenGame' : cart.name == choosenGame}"
              @click.prevent="chooseGame(cart.name)"
              :style="[`background-image: url(${cart.img}); background-size: cover; background-position: center;`]">
              <div class="cart-desc">
                <h1>{{ cart.name }}</h1></div> </div>
            </div>
            <router-link
            :to="choosenGame"
            >
                <button
                :disabled="!choosenGame"
                class="btn btn-primary btnn">
                Let's go
                </button> 
                <button
                 @click.prevent="test()"
                >
                    PRGRERETGGRVE
                </button>
            </router-link>
         
    </div>
</template>
<script>
export default {
    created() {
        
        this.$route.params
        console.log(this.$route.params)
    },
    props: ['carts'],
    data() {
        return {
            choosenGame:""
        }
    },
    methods: {
        chooseGame(pageName) {
            if (this.choosenGame == pageName) {
                return this.choosenGame = ""
            }
            this.choosenGame = pageName
    },
   
    }
}

</script>

