<template>
    <div id="gameContainer">
            <div 
             id="cells-container"
             class="cells-container"
             :class="{ 'translateUp':aOn, 'containerActivation':aOn }"
             > 
                <div v-for="(cell, index) in cellsData"
                 class="cell"
                 @click.prevent="clickCell(cell.name, index)"
                 :class="[ cell.name, {'cellClosed':!checkIfValueExists(cell.name, index),
                 'flipped':checkIfValueExists(cell.name, index),
                 'flippedReverse':!checkIfValueExists(cell.name, index)}
                  ]"
                 :style="`background-image:url(${cell.img})`"
                 key="index"
                 :data-name="`${cell.name}`"
                    >
                </div>
            </div>
            <div id="info-board">
                <div id="info-board-top">
                    <h1> {{ `your score: ${matchedCells.length}` }} </h1>
                    <button
                    class="btn btn-primary"
                    @click.prevent="randomizeCells(); gameStarted = true" >
                    {{ gameStarted == true? 'Reset game' : 'Play game'}}
                    </button>  
                </div>
            </div>
            

    </div>
</template>
<script>
export default {
    created() {
        this.randomizeCells()
    },
    props:[ 'couplesData' ],
    computed: {
        
    },
    data() {
        return {
            gameStarted: false,
            cellsData:[],
            aOn:false,
            matchedCells:[],
            checkedCells:[]
        }
    },
    methods: {
      randomizeCells() {
      const randomSort = () => Math.random() - 0.5;
      this.aOn = true
      this.cellsData = this.couplesData.sort(randomSort)
      setTimeout(() => {
      this.aOn = false;
    }, 1500); 
    },
    clickCell(cellName, index) {
        this.checkedCells.push(index)
      /*  console.log(this.checkedCells)*/
        if(this.checkedCells.length > 1) {
           console.log(this.checkedCells)
            if (this.matchingCells(this.checkedCells[0]) == this.matchingCells(this.checkedCells[1])) {
                this.matchedCells.push(cellName)
                this.checkedCells = [] 
                if (this.matchedCells.length > 14) {
                    this.matchedCells = []
                    this.checkedCells = []
                }
            } else if (this.matchingCells(this.checkedCells[0]) !== this.matchingCells(this.checkedCells[1])) {
                if (this.checkedCells.length%2 == 0) {
                setTimeout(() => {
                    this.checkedCells.splice(0,2)
                },1600)
                }  
            }
        } 
    },checkIfValueExists(cellName, index) {
      if (this.checkedCells.includes(Number(index))) {
        return true
      }else if (this.matchedCells.includes(cellName)) {
        return true
      }
    },
    matchingCells(cellIndex) {
       return document.querySelectorAll('.cell')[cellIndex].getAttribute("data-name")
    }
    

    }
}
</script>
<style>
#gameContainer {
    display: flex;
    height: 100vh;
    width: 100vw;
    justify-content: center;
}
#cells-container {
    height: 100vh;
    width: 60vw;
    padding: 20px 0 20px 0;
    display: grid;
    gap: 20px;
    row-gap: 20px;
    grid-template-columns: 120px 120px 120px 120px 120px ;
    margin-left: 18px;
}
.cell {
    border-radius: 10px;
    background-size: cover;
    background-size: cover;
    border: 2px black solid;
    width: 120px;
    height: 120px;
}


#info-board {
    width: 45vw;
    height: 100vh;
}
#info-board-top h1{
    font-weight: 700;
}


.cell.flipped {
    transform: rotateY(180deg);
    pointer-events: none;
    transition: 0.6s;
}
.cell.flippedReverse {
    transform: rotateY(-180deg);
    transition: 0.6s;
}

.containerActivation {
    animation: shake 1.5s ease-in-out infinite;
}
.cellClosed {
    background-color: black;
    background-image: none;
    position: relative;
}

.cellClosed::after {
    content: '';
    background-color: black;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0.5;
    
    transition: 3s;
}
.disable-interaction {
    pointer-events: none;
}

@keyframes shake {
      0%, 100% {
        transform: translateX(0);
      }
      25%, 75% {
        transform: translateX(-15px);
        transform: translateX(-15px);
      }
      50% {
        transform: translateX(10px);
        transform: rotateY(15deg);
      }
    }
</style>