import { createRouter, createWebHashHistory } from "vue-router"; 
import MainMenu from "./components/MainMenu.vue";
import CoupleGame from "./components/CoupleGame.vue";
import BodyPart from "./components/BodyPart.vue";
import LogoGame from "./components/LogoGame.vue";
const router = createRouter({
    history:createWebHashHistory(),
    routes: [
        {path: '/:index?', component: MainMenu},
        {path: '/couple', component: CoupleGame},
        {path: '/Body part', component: BodyPart},
        {path: '/Logo', component: LogoGame},

    ]

})

export default router;